const wanhuaUtils = {
	killLevel: 0.35, // 
	kill(ctrl) {
		// 
		if (ctrl.hasBuff('')) {
			const juanliu = ctrl.getActiveBuff('');
			juanliu.level--;
			if (juanliu.level === 0) {
				ctrl.deleteSelfBuff('');
			}
		} else {
			const juanliu = ctrl.getBuff('');
			juanliu.level = 10;
			ctrl.addBuff(juanliu);
		}
	},
	generalSkillPrepare(ctrl, skill) {
		if (ctrl.hasBuff('') && skill.type == 'ota') {
			skill.type = 'instant';
			ctrl.getActiveBuff('').level --;
		}
	},
};

module.exports = wanhuaUtils;
